/* 
#101820FF - black
#FEE715FF - yellow
*/

* {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
}

body {
  margin: 0;
}

button {
  font: inherit;
  cursor: pointer;
  border: 1px solid #ccc;
  color: #101820ff;
  background: #fee715ff;
  padding: 0.35rem 1.25rem;
  border-radius: 5px;
}

button:focus {
  outline: none;
}

button:hover,
button:active {
  background: #444444;
  border-color: #444444;
  color: #fee715ff;
}
